$background-desktop: url("./destination/background-destination-desktop.jpg");
$background-tablet: url("./destination/background-destination-tablet.jpg");
$background-mobile: url("./destination/background-destination-mobile.jpg");

$color1: white;

#destination{
    min-height: 100vh;
    background-image: $background-desktop;
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: 20px;
    color: $color1;
    box-sizing: border-box;

    section{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        margin-top: 100px;
        box-sizing: border-box;

        img{
            width: 80%;
            max-width: 350px;
            margin-left: auto;
        }

        h3{
            margin: 0 0 50px 0;
            font-weight: 300;
            font-size: 1.5em;
            text-transform: uppercase;
            text-align: center;

            span{
                font-weight: 700;
                color: grey;
            }
        }

        &>div{
            width: 41%;
            margin: 10px 20px;
            position: relative;
            height: auto;

            &:nth-child(1){
                display: flex;
                flex-direction: column;
            }

            // &:nth-child(2){

            // }
        }

        .planet-toggle{
            display: flex;
            flex-wrap: nowrap;
            justify-content: space-between;
            margin-top: 60px;

            a{
                display: inline-block;
                border-bottom: 3px solid rgba(255, 255, 255, 0);
                text-transform: uppercase;
                font-weight: 300;
                padding: 5px 0;

                &:hover{
                    border-bottom: 3px solid rgb(121, 121, 121);
                    cursor: pointer;
                }

                &.active{
                    border-bottom: 3px solid rgba(255, 255, 255);
                }
            }
        }

        h1{
            font-size: 5em;
            font-weight: 300;
            text-transform: uppercase;
            margin: 30px 0;
        }

        p{
            font-weight: 300;
        }

        hr{
            height: 1px;
            border: none;
            border-bottom: 1px solid white;
        }

        .planet-facts{
            display: flex;
            justify-content: space-between;
            
            &>div{
                width: 45%;
                
                h4{
                    text-transform: uppercase;
                    font-weight: 300;
                    font-size: 0.95em;
                    margin: 10px 0;
                }

                p{
                    text-transform: uppercase;
                    font-weight: 400;
                    font-size: 1.5em;
                    margin: 5px 0;
                }
            }
        }
    }
}

@media screen and (width < 700px){
    #destination{
        background-image: $background-tablet;

        section{
            flex-direction: column;

            div{
                width: 90%;
                margin: auto;

                img{
                    margin: auto;
                }
            }
        }
    }
}

@media screen and (width < 450px){
    #destination{
        background-image: $background-mobile;

        section{
            h1{
                font-size: 4.5em;
            }
        }
    }
}