$background-desktop: url(./crew/background-crew-desktop.jpg);
$background-mobile: url('./crew/background-crew-mobile.jpg');
$background-tablet: url('./crew/background-crew-tablet.jpg');

$color1: white;
$color2: grey;

#crew{
    background-image: $background-desktop;
    background-size: cover;
    background-repeat: no-repeat;
    color: $color1;
    min-height: 100vh;
    overflow-y: hidden;

    section{
        margin-top: 120px;
        height: 100%;
        box-sizing: border-box;
        display: flex;
        flex-wrap: nowrap;
        justify-content: center;

        
        &>div{
            width: 42%;
            position: relative;

            img{
                width: 70%;
                position: absolute;
                bottom: 0;
            }

            span{
                color: $color2;
            }

            h3{
                font-size: 1.4em;
                text-transform: uppercase;
                font-weight: 400;
                margin: 30px 0;

                span{
                    font-weight: 700;
                }
            }

            h2{
                text-transform: uppercase;
                font-weight: 400;
                font-size: 1.8em;
                margin: 100px 0 5px 0;
            }

            h1{
                text-transform: uppercase;
                font-weight: 400;
                font-size: 2em;
                margin: 10px 0 40px 0;
            }

            p{
                font-weight: 300;
                font-size: 1.02em;
                width: 90%;
            }

            .navi{
                display: flex;
                margin: 40px 0;

                $size: 20px;
                &>div{
                    width: $size;
                    height: $size;
                    border-radius: 50%;
                    background-color: $color2;
                    margin-right: 30px;

                    &:hover{
                        background-color: $color1;
                        cursor: pointer;
                    }
                    
                    &.active{
                        background-color: $color1;
                    }
                }
            }
        }
    }
}

@media screen and (width < 650px){
    #home{
        section{
            flex-direction: column;
            margin: initial;
            margin-top: 120px;
            
            div{
                width: 80%;
                margin: auto;
                padding-bottom: 40px;

                &:nth-child(2){
                    justify-content: center;
                }
            }
        }
    }
}

@media screen and (width < 850px){
    #crew{
        background-image: $background-tablet;

        section{
            margin-top: 70px;

            &>div{
                h2{
                    margin-top: 50px;
                }

                h1{
                    margin-bottom: 30px;
                }
            }
        }
    }
}

@media screen and (width < 650px){
    #crew{
        section{
            flex-direction: column;

            & > div{
                width: 90%;
                margin: auto;
                
                img{
                    position: initial;
                    display: inline-block;
                    max-width: 270px;
                    margin: auto;
                }

                .navi{
                    justify-content: center;
                }
            }
        }
    }
}

@media screen and (width < 450px){
    #crew{
        background-image: $background-mobile;
    }
}

