$color1: white;

nav{
    display: flex;
    justify-content: space-between;
    height: 60px;
    position: fixed;
    width: 100%;
    top: 20px;   
    box-sizing: border-box;
    z-index: 1;
    overflow: visible;

    a{
        &>img{
            margin-left: 30px;
        }
        
    }
    
    img{
        align-self: center;
    }

    .nav-links-mobile{
        display: none;
    }

    .nav-links{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        width: 55%;
        min-width: 600px;
        height: 100%;
        padding-left: 30px;
        box-sizing: border-box;
        background-color: rgba(50, 49, 69, 0.644);

        &.hide{
            display: none;
        }

        a{
            
            line-height: 320%;
        }
    }

    ul{
        list-style: none;
        margin: 0;
        padding: 0;
        height: 100%;
    }

    li{
        display: inline-block;
        height: 100%;
        padding: 0 15px;
        font-size: 1.1em;
        text-transform: uppercase;
        
        a{
            color: $color1;
            text-decoration: none;
            display: inline-block;
            height: 100%;
            box-sizing: border-box;
            border-bottom: 4px solid rgba(255, 255, 255, 0);
            
            &:hover, &.active{
                border-bottom: 4px solid white;
            }
        }

    }
}

@media screen and (width < 950px){
    nav{
        background-color: rgba(50, 49, 69, 0.644);
        top: 0;

        .nav-links{
            background-color: rgba(50, 49, 69, 0);
        }

        img{
            margin-top: 5px;
            cursor: pointer;
        }
    }

}

@media screen and (width < 650px){
    nav{
        
        background-color: rgb(0, 4, 18);
        top: 0;

        .nav-links{
            display: none;
        }
        
        .nav-links-mobile{
            display: flex;
            flex-direction: column;
            min-width: 50%;
            right: 20px;
            position: relative;
    
            &>img{
                margin-left: auto;
                padding-right: 30px;
                position: relative;
                top: 15px;
                width: 28px;
    
                &:hover{
                    cursor: pointer;
                    line-height: 350%;
                }
            }

            section{
                background-color: rgb(0, 4, 18);
                display: flex;
                flex-direction: column;
                position: absolute;
                justify-content: flex-start;
                right: 0;
                width: 190px;

                img{
                    align-self: flex-end;
                    width: 28px;
                    margin: 20px 30px;
                }
            }

            
            
            &.hide{
                display: none;
            }
        }

        li{
            display: block;
            box-sizing: border-box;
            margin: 10px 10px;

            a{
                width: 100%;
            }
        }
    }
}