$background-desktop: url("./technology/background-technology-desktop.jpg");
$background-mobile: url("./technology/background-technology-mobile.jpg");
$background-tablet: url("./technology/background-technology-tablet.jpg");

$color1: white;
$color2: gray;

#tech{
    background-image: $background-desktop;
    min-height: 100dvh;
    color: $color1;
    overflow-y: auto;

    section{
        margin-top: 120px;
        display: flex;
        justify-content: center;
        
        &>div{
            width: 40%;
            margin: 20px;

            span{
                color: $color2;
            }

            h3{
                font-size: 1.4em;
                text-transform: uppercase;
                font-weight: 400;
                margin: 30px 0;

                span{
                    font-weight: 700;
                }
            }

            img{
                width: 100%;
                object-fit: contain;
            }

            &:nth-child(2){
                display: flex;
                flex-wrap: nowrap;
                align-items: flex-end;
            }
        }

        .toggle-navi{
            display: flex;
            flex-wrap: nowrap;

            .navi{
                width: 70px;

                .navi-circle{
                    $size: 50px;

                    width: $size;
                    height: $size;
                    text-align: center;
                    font-weight: 700;
                    font-size: 1.2em;
                    background-color: black;
                    border-radius: 50%;
                    margin: 20px 20px 0 0;
                    padding-top: 10px;
                    box-sizing: border-box;
                    cursor: pointer;
                    
                    &.active{
                        background-color: $color1;
                        color: black;
                    }

                    &:hover{
                        background-color: $color2;
                    }
                }
            }
        }
    }
}

@media screen and (width < 750px){
    #tech{
        background-image: $background-tablet;
    }
}

@media screen and (width < 700px){
    #tech{
        section{
            flex-direction: column-reverse;

            &>div{
                width: 90%;

                img{
                    max-width: 400px;
                    margin: auto;
                }
            }
        }
    }
}