$background-desktop: url("./home/background-home-desktop.jpg");
$background-tablet: url("./home//background-home-tablet.jpg");
$background-mobile: url("./home/background-home-mobile.jpg");

$color1: white;

#home{
    min-height: 100vh;
    background-image: $background-desktop;
    background-size: cover;
    background-repeat: no-repeat;
    color: $color1;
    box-sizing: border-box;
    padding-top: 20px;

    section{
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        margin: 120px 0;
        box-sizing: border-box;

        div{
            max-width: 400px;

            &:nth-child(2){
                display: flex;
                flex-wrap: nowrap;
                align-items: center;
            }

            h1{
                font-size: 6em;
                font-weight: 400;
                text-transform: uppercase;
                margin: 10px 0;
            }

            h3{
                font-size: 1.2em;
                text-transform: uppercase;
                font-weight: 400;
            }
            
            $btn-size: 250px;

            button{
                width: $btn-size;
                height: $btn-size;
                border: none;
                border-radius: 50%;
                font-size: 2em;
                text-transform: uppercase;

                &:hover{
                    cursor: pointer;
                    box-shadow: 0 0 10px 10px rgba(255, 255, 255, 0.468), 0 0 10px 20px rgba(255, 255, 255, 0.228);
                }
            }
        }
    }
}

@media screen and (width < 650px){
    #home{
        section{
            flex-direction: column;
            margin: initial;
            margin-top: 120px;
            
            div{
                width: 80%;
                margin: auto;
                padding-bottom: 40px;

                &:nth-child(2){
                    justify-content: center;
                }
            }
        }
    }
}

@media screen and (width < 700px){
    #home{
        background-image: $background-tablet;
    }
}

@media screen and (width < 450px){
    #home{
        background-image: $background-mobile;
        section{
            div{
                h1{
                    font-size: 4.5em;
                }
            }
        }
    }
}


.hide{
    display: none;
}